@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap');

.container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #15273F;
    width: 15rem;
    min-height: 25rem;
    border-radius: 15px;
    display: flex;
    z-index: 0;
    
    flex-direction: column;
    padding: 20px 20px 0px 20px;
    
    
}

.view_icon{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin-top: -90px;
    width: 240px;
    height: 240px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    background-color: hsla(178, 100%, 50%, 0.50);
    cursor: pointer;

}

.view_icon:hover{
    opacity: 1;
}

.equilibrium{
    width: 240px;
    border-radius: 10px;
    
}

h1{
    font-size: 18.5px;
    font-family: 'Outfit', sans-serif;
    color: hsl(0, 0%, 100%);
    cursor: pointer;
    font-weight:400 ;
    letter-spacing: 0.02rem;
}
h1:hover{
    color: hsl(178, 100%, 50%);
    cursor: pointer;
}

p{
    margin-top: 0;
    font-size: 13.5px;
    font-weight: 200;
    line-height: 26px;
    color: hsl(215, 51%, 70%);
    letter-spacing: .0001rem;
    font-family: 'Outfit', sans-serif;
    line-height: 1.6;
}

.bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.eth img{
    width: 9px;
    align-items: center;
    margin-right: 5px;
}

.eth {
    font-family: 'Outfit', sans-serif;
    font-weight:505 ;
    color: hsl(178, 100%, 50%);
}

.clock img{
    width: 15px;    
    margin-right: 5px;
    vertical-align:sub;
}



.horizontal_line{
    
    height: 1px;
    width: 15rem;
    background-color: hsl(215, 51%, 70%);
}
.id{
    margin:1rem 0 0 0;
    display: flex;
    flex-direction: row;
}

.id p img{
    width: 25px;
    vertical-align: middle;
    margin-right: 10px;
    border:0.1px solid white;
    border-radius: 100%;
}

span{
    color:aliceblue;
}

.id p span:hover{
    color: hsl(178, 100%, 50%);
    cursor: pointer;
}
